import Image from "next/image"
import { MagnifyingGlassIcon, GlobeAltIcon, Bars3Icon, UserIcon } from '@heroicons/react/24/solid'
function Header(){
    return(
        <header className="sticky top-0 z-50 grid grid-cols-3 bg-white shadow-md py-5 px-5 md:px-10">
            <div className="relative flex items-center h-10">
                  <Image src="/R.webp" layout="fill" alt="airbnb image"
            objectFit="contain" objectPosition="left"/>
            </div>
            <div className="flex items-center md:border-1 rounded-full py-2 shadow-lg">
                <input className="flex-grow pl-5 bg-transparent outline-none" type="text" placeholder="Search here" />
                <MagnifyingGlassIcon className="md:mx-2 hidden md:inline-flex h-8 bg-red-400 text-white rounded-full p-2"/>
            </div>
            <div className="flex items-center justify-end space-x-4 text-gray-500">
                <p className="hidden md:inline">Become a host</p>
                <GlobeAltIcon className="h-8"/>
                <div className="flex rounded-full shadow-lg items-center space-x-2 border-2 p-2">
                  <Bars3Icon className="h-6"/>
                  <UserIcon className="h-6" />
                </div>
            </div>
        </header>
    )
}
export default Header